import https from 'https';
import http from 'http';
import express from "express";
import bodyParser from "body-parser";
import fs from "fs"
import databox from 'node-databox';

const DATABOX_ZMQ_ENDPOINT = process.env.DATABOX_ZMQ_ENDPOINT
const credentials = databox.getHttpsCredentials();

const PORT = process.env.port || '8080';

const kvc = databox.NewKeyValueClient(DATABOX_ZMQ_ENDPOINT, false);

let metaData = databox.NewDataSourceMetadata();

const profileAge = {
    ...metaData,
    Description: 'User profile: age',
    ContentType: 'application/json',
    Vendor: 'Databox Inc.',
    DataSourceType: 'profileAge',
    DataSourceID: 'profileAge',
    StoreType: 'kv',
}

const profileEyeColour = {
    ...metaData,
    Description: 'User profile: eye colour',
    ContentType: 'application/json',
    Vendor: 'Databox Inc.',
    DataSourceType: 'profileEyeColour',
    DataSourceID: 'profileEyeColour',
    StoreType: 'kv',
}

const forEachPromise = (items, fn, results = []) => {
    return items.reduce(function (promise, item) {
        return promise.then((x = []) => {
            return fn(item, [...results, ...x]);
        });
    }, Promise.resolve([]));
}

const read = (datasourceid, results) => {
    return new Promise((resolve, reject) => {
        kvc.Read(datasourceid, "attribute").then((result) => {
            console.log("result:", datasourceid, result);
            resolve([...results, result]);
        }).catch((err) => {
            console.log("error reading for", datasourceid);
            resolve([...results]);
        });
    });
}

kvc.RegisterDatasource(profileAge).then(() => {
    console.log("registered age data source");
    return kvc.RegisterDatasource(profileEyeColour)
}).then(() => {
    console.log("registered eye colour data source");
});

console.log("[Creating https server]", PORT);
const app = express();

app.set('views', './views');
app.set('view engine', 'html');
app.engine('html', require('ejs').renderFile);
app.use(bodyParser.urlencoded({ extended: false }));
app.use(bodyParser.json());

app.use('/', express.static('./www'));

app.get('/ui', (req, res) => {
    console.log("in ui endpoint!!");
    res.render('index');
});


app.get('/ui/getPhysicalAttributes', (req, res) => {
    console.log("getting physical attributes");
    const items = ["profileAge", "profileEyeColour"];
    forEachPromise(items, read).then((results) => {
        console.log('done', results);
        const payload = results.reduce((acc, item) => {
            const { key, value } = item;
            return { ...acc, [key]: value };
        }, {});
        console.log(payload);
        res.send(payload);
    });
});

app.post('/ui/setPhysicalAttributes', (req, res) => {
    console.log("setting physical attributes!");
    console.log(JSON.stringify(req.body));

    if (!req.body || !req.body.attributes) {
        res.send({ success: true });
        return;
    }

    const attributes = Object.keys(req.body.attributes || []);

    const promises = attributes.map((k) => {
        return new Promise((resolve, reject) => {
            kvc.Write(k, "attribute", { "key": k, "value": req.body.attributes[k] }).then(() => {
                console.log("successfully written!");
                resolve();
            }).catch((err) => {
                console.log("failed to write", err);
                reject(err);
            });
        })
    });

    return Promise.all(promises).then(() => {
        console.log("finished writing!")
        res.send({ success: true });
    }, (err) => {
        res.send({ success: false, err });
    });
});

app.get("/status", function (req, res) {
    res.send("active");
});


https.createServer(credentials, app).listen(PORT);


